@keyframes zoom {
  0% {
    transform: scale(1);
  }
  100% {
    transform: scale(1.3);
  }
}

.main {
  display: flex;
  flex-direction: column;
  justify-content: center;
  overflow-x: hidden;
}

.main > * {
  width: auto;
}

.footer {
  position: relative;
  bottom: 0;
  width: auto;
  text-align: center;
}
.footer > footer > p {
  color: rgb(255, 255, 255);
  font-size: small;
}

.shadow {
  animation: changeFilter 8s linear infinite;
}
@keyframes changeFilter {
  0% {
    filter: drop-shadow(5px -5px 7px #80dfff);
  }

  15% {
    filter: drop-shadow(5px -5px 7px #cc6600);
  }
  30% {
    filter: drop-shadow(5px -5px 7px #00ccff);
  }
  45% {
    filter: drop-shadow(5px -5px 7px #ffff00);
  }

  60% {
    filter: drop-shadow(5px -5px 7px #ff00ff);
  }

  70% {
    filter: drop-shadow(5px -5px 7px #00cc00);
  }

  85% {
    filter: drop-shadow(5px -5px 7px #00ccff);
  }
  100% {
    filter: drop-shadow(5px -5px 7px #ff9900);
  }
}

@keyframes changeText {
	0% {
	  color: #80dfff;
	}
  
	15% {
	  color: #cc6600;
	}
	30% {
	  color: #00ccff;
	}
	45% {
	  color: #ffff00;
	}
  
	60% {
	  color: #ff00ff;
	}
  
	70% {
	  color: #00cc00;
	}
  
	85% {
	  color: #00ccff;
	}
	100% {
	  color: #ff9900;
	}
  }

  @keyframes changeBorder {
	0% {
	  border: 3px solid #80dfff;
	}
  
	15% {
	  border: 3px solid #cc6600;
	}
	30% {
	  border: 3px solid #00ccff;
	}
	45% {
	  border: 3px solid #ffff00;
	}
  
	60% {
	  border: 3px solid #ff00ff;
	}
  
	70% {
	  border: 3px solid #00cc00;
	}
  
	85% {
	  border: 3px solid #00ccff;
	}
	100% {
	  border: 3px solid #ff9900;
	}
  }

@media screen and (max-width: 760px) {
  body {
    overflow-x: hidden;
  }
  .main > * {
    width: auto;
    /* margin-top: 3%; */
  }

  .footer {
    margin-top: 7%;
  }
  .footer > footer > p {
    font-size: small;
  }
}

@media screen and (min-width: 300px) and (max-width: 480px) {
  body {
    overflow-x: hidden;
  }
  .main {
    /* margin-top: 5%; */
  }
  .main > * {
    width: auto;
  }
  .footer {
    /* margin-top: 4%; */
  }
  .footer > footer > p {
    font-size: x-small;
  }
}
